<template>
<div>
  <!-- a-table @change  
  :pagination="false" 
  -->
  <a-table 
    :columns="columns"
    :data-source="data" 
    :scroll="{ x: 1500}"
    :pagination="pagination"
    :loading="loading"
    size="small"
    @change="handleTableChange"
    >
    <template slot="patStatus" slot-scope="text">
       {{ $GlobalDict.Consultation.FormatApprovalStatus(text)}}
    </template>
    <span slot="action" slot-scope="text, record">
      <template>
        <a @click="openEditForm(record)"></a>
      </template>
    </span>
  </a-table>
  <!-- EditFormpropsvisible visible.sync  this.$emit('update:visible',false); props-->
  <edit-form
    ref="editForm"
    :visible.sync="formVisible"
    :form-model="formModel"
  />  
</div>
</template>
<script>
import editForm from '@/views/Consultation/EditForm'

const columns = [
  // { title: '', width: 60, dataIndex: 'patbed', key: 'key', fixed: 'left' },
  { title: '', width: 80, dataIndex: 'inpno', key: 'age', fixed: 'left' },
  { title: '', dataIndex: 'patientid', key: '1', width: 220 , fixed: 'left'},
  // { title: '', dataIndex: 'patname', key: '1', width: 220 , fixed: 'left',scopedSlots: { customRender: 'name' },},
    // { title: '_', dataIndex: 'gender', key: '1', width: 120 },
    // { title: '_Email', dataIndex: 'email', key: '1', width: 230 },
    // { title: '', dataIndex: 'address', key: '2', width: 150 },
    // { title: '', dataIndex: 'address', key: '3', width: 150 },
    // { title: '', dataIndex: 'address', key: '4', width: 150 },
    // { title: '', dataIndex: 'address', key: '5', width: 150 },
  { title: '', dataIndex: 'applydoctor', key: 'applydoctor', width: 150 },
  { title: '', dataIndex: 'consultationtype', key: 'consultationtype', width: 150 },
  { title: '', dataIndex: 'consultationpurpose', key: 'consultationpurpose', ellipsis: true},
  { title: '', dataIndex: 'applydate', key: 'applydate',ellipsis: true},
  // { title: '', dataIndex: 'address', key: '8' },
  { title: '', dataIndex: 'pat_status', key: '9',scopedSlots: { customRender: 'patStatus' }, },
  {
    title: '',
    key: 'operation',
    fixed: 'right',
    width: 100,
    scopedSlots: { customRender: 'action' },
  },
];


const data = [];

export default {
  components: {
    editForm,
  },
  props: {//, props :title="xxxx"  title="xxxx"
    queryParam: {},// 
  },
  data() {
    return {//return
      //
      data,
      columns,
      pagination:{hideOnSinglePage:true},//hideOnSinglePage 
      loading: true,
      formVisible: false,
      formModel:{},
    };
  },
  mounted() {//mounted 
    this.fetch();
  },
  watch: {
    queryParam: {
      handler() {
        this.fetch();
      },
      deep: true,//
      // immediate: true
    }
  },
  methods: {
    //a-table @change 
    handleTableChange(pagination, filters, sorter) {
      console.log(pagination);
      const pager = { ...this.pagination };
      pager.current = pagination.current;
      this.pagination = pager;
      this.fetch({
        results: pagination.pageSize,
        page: pagination.current,
        sortField: sorter.field,
        sortOrder: sorter.order,
        ...filters,
      });
    },
    //
    fetch() {
      this.loading = true;
      // let getParams = {
      //   results: 10,
      //   ...params,
      // };
      // let postParams = "?applyuser="+this.$GlobalData.LoginUserInfo.userName;//000324
      // if(Object.keys(this.queryParam).length!=0){
      //   getParams += "&patStatus="+ this.queryParam.status; toDate
      // }
      let postParams = {
        "starttime": this.queryParam.applyDateRegion[0].format("YYYY-MM-DD"),
        "endtime": this.queryParam.applyDateRegion[1].format("YYYY-MM-DD"),
        ...this.queryParam,
      };

      console.log('postParams',postParams)
      this.$Http.Post(this.$Api.Consultation.ApplyList,postParams).then(res => {
          const pagination = { ...this.pagination };
          // Read total count from server
          // pagination.total = data.totalCount;
          pagination.total = 1;
          this.loading = false;
          console.log("res.data",res.data)
          this.data = res.data;
          this.pagination = pagination;
      });
    },
    openEditForm (record) {//
      console.log(record)
      this.formModel = { ...record }//
      this.formVisible = true;
    }
  },
};
</script>
